import React from "react";
import Comment from "./Comment.js";
import ApprovalCard from "./ApprovalCard";

function Comments() {
  const C = [
    {
      id: 0,
      author: "Bob",
      date: "5:10PM",
      comment: "How boring"
    },
    {
      id: 1,
      author: "Jacob",
      date: "12:11AM",
      comment: "Dude your so good!"
    },
    {
      id: 2,
      author: "Justin",
      date: "3:30AM",
      comment: "I still believe!"
    }
  ];
  return (
    <div>
      {C.map(commentItem => (
        <ApprovalCard>
          <Comment
            key={commentItem.id}
            author={commentItem.author}
            date={commentItem.date}
            comment={commentItem.comment}
          />
        </ApprovalCard>
      ))}
    </div>
  );
}

export default Comments;
